.improve-page {
	@extend .about-section;
	.message-wrap {
		// margin: 130px 30px
		@include pos(rel,null);
		@include media-breakpoint-down(lg) {
 }			// margin: 80px 100px
		@include media-breakpoint-down(sm) {
			margin: 0; }
		img {
			max-width: unset; }
		.message-content {
			background: $white;
			padding: 30px;
			@include pos(rel,null);
			z-index: 5;
			@include media-breakpoint-down(sm) {
				padding: 0; }
			.content {
				padding: 30px;
				height: 650px;
				overflow: auto;
				@include media-breakpoint-down(lg) {
					height: 500px; }
				@include media-breakpoint-down(sm) {
					padding: 15px;
					height: 300px; }
				&::-webkit-scrollbar-track {
					background-color: darken($white,10%); }
				&::-webkit-scrollbar {
					width: 4px;
					background-color: darken($white,10%); }
				&::-webkit-scrollbar-thumb {
					background-color: $green; } }
			.author {
				text-align: right;
				font-style: italic;
				margin-top: 20px;
				strong {
					display: block;
					font-style: normal; } } }
		.img1 {
			@include pos(abs, 0 0 null null);
			z-index: 1;
			@include transform(translate(50%,-50%));
			@include media-breakpoint-down(lg) {
				width: 30%; }
			@include media-breakpoint-down(sm) {
				display: none; } }
		.img2 {
			@include pos(abs,100px 0 null null);
			z-index: 2;
			@include transform(translate(100%,0));
			@include media-breakpoint-down(lg) {
				width: 30%;
				top: 50px; }
			@include media-breakpoint-down(sm) {
				display: none; } }
		.img3 {
			@include pos(abs,null null 100px 0);
			z-index: 2;
			@include transform(translate(-100%,0));
			@include media-breakpoint-down(lg) {
				width: 30%;
				bottom: 50px; }
			@include media-breakpoint-down(sm) {
				display: none; } }
		.img4 {
			@include pos(abs, null null 0 0);
			z-index: 1;
			@include transform(translate(-50%,50%));
			@include media-breakpoint-down(lg) {
				width: 30%; }
			@include media-breakpoint-down(sm) {
				display: none; } } } }
.she-system {
	@extend .about-section;
	.she-tab {
		@extend .child-nav;
		background: none;
		margin-bottom: 30px;
		ul {
			border-top: none;
			margin: 0;
			border: none;
			li {
				margin: 0;
				a {
					padding: 10px 0;
					margin: 0;
					border: none;
					&.active {
						background: none;
						color: $green;
						&:after {
							width: 60px; } } } } } }
	.she-img {
		@include media-breakpoint-down(md) {
			margin-bottom: 30px; }
		img {
			width: 100%; } }
	.she-content {
		color: darken($white,60%);
		@include fs(14rem);
		ul {
			li {
				padding-left: 20px;
				@include pos(rel,null);
				margin-bottom: 5px;
				&:before {
					content: '\f00c';
					@include pos(abs, 0 null null 0); } } } } }

.improve-news-wrap {
	@extend .about-section; }

.improve-blog {
	// padding: 50px 0
	@include media-breakpoint-down(md) {
		padding-bottom: 0; }
	.blog-list {
		.row {
			margin: 0px -10px; }
		.col-12 {
			padding: 0px 10px; }
		.blog-col {
			margin-bottom: 15px;
			padding-bottom: 15px;
			border-bottom: 1px solid darken($white,10%);
			figure {
				@include flexbox;
				flex-wrap: wrap;
				.blog-img {
					display: block;
					width: 160px;
					@include media-breakpoint-down(lg) {
						width: 120px; }
					img {
						width: 100%; } }
				figcaption {
					width: calc(100% - 160px);
					padding-left: 20px;
					@include media-breakpoint-down(lg) {
						padding-left: 10px; }
					time {
						@include fs(12rem);
						color: darken($white,40%);
						font-weight: 300;
						display: block;
						margin-bottom: 5px;
						&:before {
							content: '\f073';
							margin-right: 5px; } }
					.blog-name {
						line-height: 1.5;
						@include fs(14rem);
						font-weight: 300;
						display: -webkit-box;
						height: 0.875rem*1.5*2;
						-webkit-line-clamp: 2;
						-webkit-box-orient: vertical;
						overflow: hidden;
						text-overflow: ellipsis;
						a {
							color: $main;
							&:hover {
								color: $blue; } } } } }
			&.big {
				border-bottom: none;
				padding-bottom: 0;
				margin-bottom: 0;
				figure {
					.blog-img {
						width: 100%; }
					figcaption {
						width: 100%;
						padding: 15px 0;
						.blog-name {
							@include fs(16rem);
							font-weight: 500;
							margin-bottom: 15px;
							@include media-breakpoint-down(lg) {
								@include fs(15rem);
								font-weight: 600; } }
						.blog-des {
							@include fs(14rem);
							font-weight: 300;
							display: -webkit-box;
							height: 0.875rem*1.5*4;
							-webkit-line-clamp: 4;
							-webkit-box-orient: vertical;
							overflow: hidden;
							text-overflow: ellipsis;
							@include media-breakpoint-down(lg) {
								height: 0.875rem*1.5*3;
								-webkit-line-clamp: 3; } } } } } } } }

.document-download {
	// padding: 50px 0
	@include media-breakpoint-down(md) {
		padding-top: 0; }
	.table {
		td {
			padding: 15px;
			@include fs(14rem);
			border: none;
			background: none;
			@include media-breakpoint-down(lg) {
				padding: 10px;
				@include fs(13rem); }
			&:first-child {
				white-space: nowrap;
				overflow: hidden;
				text-overflow: ellipsis; }
			a {
				color: $green;
				&:hover {
					color: $blue; } } }
		tr {
			&:nth-child(odd) {
				background: darken($white,4%); } } } }
