.homecorp {
	main {
		background: url(../img/cpbg.png) center top no-repeat; } }

.home-title {
	@include fs(24rem);
	text-transform: uppercase;
	color: $green;
	font-weight: 600;
	margin-bottom: 30px;
	@include media-breakpoint-down(lg) {
		@include fs(20rem); }
	@include media-breakpoint-down(md) {
		@include fs(18rem); }
	&.icon {
		padding-left: 30px;
		@include pos(rel,null);
		@include fs(20rem);
		font-weight: 600;
		color: $blue;
		@include media-breakpoint-down(sm) {
			@include fs(16rem); }
		&:before {
			content: '';
			display: block;
			width: 31px;
			height: 30px;
			background: url(../img/home-icon.png) center center no-repeat;
			background-size: contain;
			@include pos(abs, -4px null null 0);
			@include media-breakpoint-down(sm) {
				top: -7px; } } } }
.home-des {
	@include fs(14rem);
	font-weight: 300;
	max-width: 75%;
	margin: -5px auto 30px;
	@include media-breakpoint-down(md) {
		max-width: unset; } }

.page-title {
	@include fs(24rem);
	font-weight: 700;
	line-height: 1.3;
	text-transform: uppercase;
	color: $blue;
	margin-bottom: 15px;
	@include media-breakpoint-down(lg) {
		@include fs(24rem); }
	@include media-breakpoint-down(md) {
		@include fs(20rem); }
	@include media-breakpoint-down(sm) {
		@include fs(18rem); } }
.page-des {
	// +fs(16rem)
	font-weight: 500;
	max-width: 80%;
	margin: -15px auto 30px;
	@include media-breakpoint-down(lg) {
		@include fs(14rem);
		margin-bottom: 20px; }
	@include media-breakpoint-down(md) {
		max-width: 100%; }
	@include media-breakpoint-down(sm) {
		@include fs(13rem); } }
.small-title {
	@include fs(18rem);
	font-weight: 600;
	text-transform: uppercase;
	color: $main;
	margin-bottom: 20px; }

.home-product {
	padding: 50px 0;
	.product-col {
		@include media-breakpoint-down(sm) {
			margin-bottom: 15px; }
		figure {
			@include pos(rel,null);
			.product-img {
				@include bor-rad(5px);
				overflow: hidden;
				img {
					width: 100%;
					@include bor-rad(5px); } }
			figcaption {
				@include pos(abs, null 0 0 0);
				padding: 10px 20px;
				background: color-rgba($green,0.75);
				@include bor-rad(0 0 5px 5px);
				@include media-breakpoint-down(md) {
					padding: 5px 10px; }
				.product-name {
					@include fs(22rem);
					font-weight: 600;
					@include media-breakpoint-down(md) {
						@include fs(18rem); }
					a {
						color: $white; } }
				.product-des {
					@include fs(13rem);
					font-weight: 300;
					color: $white;
					@include media-breakpoint-down(md) {
						@include fs(12rem); } }
				.product-icon {
					@include pos(abs, 50% 20px null null);
					@include transform(translateY(-50%));
					@include media-breakpoint-down(lg) {
						width: 70px; }
					@include media-breakpoint-down(md) {
						width: 50px;
						right: 10px; } } } } } }

.home-section {
	margin-bottom: 40px;
	.title-wrap {
		border-bottom: 1px solid darken($white,10%);
		padding-bottom: 10px;
		margin-bottom: 20px;
		.home-title {
			float: left;
			margin: 0; }
		.viewmore {
			float: right;
			color: $green;
			@include fs(13rem); } }
	.develop {
		@include bor-rad(5px);
		overflow: hidden;
		box-shadow: 0px 3px 3px color-rgba($black,0.1);
		margin-bottom: 20px;
		@include media-breakpoint-down(sm) {
			overflow: visible;
			padding-right: 20px;
			box-shadow: none; }
		.develop-col {
			float: left;
			width: 33.33%;
			background: #e38355;
			@include flexbox;
			align-items: center;
			@include pos(rel,null);
			@include media-breakpoint-down(sm) {
				width: 100%;
				float: none;
				margin-bottom: 10px;
				padding-left: 20px;
				@include bor-rad(5px 0 0 5px); }
			&:after {
				content: '';
				@include pos(abs, 0 null null 100%);
				width: 0;
				height: 0;
				border-left: 35px solid #e38355;
				border-top: 50px solid transparent;
				border-right: none;
				border-bottom: 50px solid transparent;
				z-index: 5;
				@include media-breakpoint-down(lg) {
					border-top-width: 40px;
					border-bottom-width: 40px;
					border-left-width: 20px; } }
			&:nth-child(2) {
				background: #49c5e9;
				padding-left: 20px;
				&:after {
					border-left-color: #49c5e9; } }
			&:nth-child(3) {
				background: #aac65b;
				padding-left: 20px;
				&:after {
					display: none;
					border-left-color: #aac65b;
					@include media-breakpoint-down(sm) {
						display: block; } } }
			.icon {
				width: 100px;
				height: 100px;
				@include flexbox;
				justify-content: center;
				align-items: center;
				@include media-breakpoint-down(lg) {
					width: 50px;
					height: 50px; } }
			.des {
				width: calc(100% - 100px);
				@include flexbox;
				align-items: center;
				align-content: center;
				color: $white;
				@include fs(12rem);
				line-height: 1.6;
				padding-right: 15px;
				@include media-breakpoint-down(lg) {
					width: calc(100% - 50px);
					height: 80px;
					padding-right: 10px;
					@include fs(12rem); } } } }
	.develop-intro {
		padding-left: 15px;
		border-left: 1px solid $green;
		@include fs(13rem);
		color: darken($white,60%); }
	.news-list {
		.news-col {
			margin: 0; } } }
.sidebar-title {
	@include pos(rel,null);
	background: $green;
	a {
		display: block;
		@include fs(12rem);
		color: $white;
		text-transform: uppercase;
		line-height: 36px;
		padding: 0px 15px;
		font-weight: 600;
		@include media-breakpoint-down(lg) {
			@include fs(10rem);
			font-weight: 700;
			padding: 0px 10px; } }
	.lnr {
		@include pos(abs, 10px 15px null null);
		@include media-breakpoint-down(lg) {
			display: none; } } }

.baocao-phattrien {
	@include bor-rad(5px);
	overflow: hidden;
	box-shadow: 0px 3px 10px color-rgba($black,0.1);
	margin-bottom: 20px;
	@include media-breakpoint-down(md) {
		width: calc(33.33% - 15px);
		float: left; }
	@include media-breakpoint-down(sm) {
		width: calc(50% - 15px); }
	@include media-breakpoint-down(xs) {
		width: 100%;
		float: none; } }
.news-sidebar {
	background: darken($white,7%);
	@include bor-rad(5px);
	overflow: hidden;
	box-shadow: 0px 3px 10px color-rgba($black,0.1);
	margin-bottom: 20px;
	@include media-breakpoint-down(md) {
		width: calc(33.33% - 15px);
		margin-left: 30px;
		float: left; }
	@include media-breakpoint-down(sm) {
		width: calc(50% - 15px); }
	@include media-breakpoint-down(xs) {
		width: 100%;
		float: none;
		margin-left: 0; }
	.post-list {
		padding: 5px 15px;
		.post {
			@include pos(rel,null);
			padding: 10px 0;
			padding-left: 20px;
			border-bottom: 1px dashed darken($white,20%);
			&:last-child {
				border: none; }
			a {
				color: darken($white,60%);
				@include fs(12rem);
				&:hover {
					color: $blue; } }
			.lnr {
				color: $green;
				@include fs(14rem);
				@include pos(abs, 10px null null 0); } } } }
.home-brand {
	@include flexbox;
	flex-wrap: wrap;
	margin-bottom: 40px;
	.label {
		@include flexbox;
		align-items: center;
		align-content: center;
		@include fs(14rem);
		font-weight: 600;
		text-transform: uppercase;
		width: 120px; }
	.brand-slide {
		width: calc(100% - 120px);
		.item {
			.brand {
				@include flexbox;
				align-items: center;
				align-content: center;
				justify-content: center;
				height: 60px;
				img {
					max-width: 100%;
					max-height: 100%; } } } } }
