.news-list {
	.news-col {
		margin-bottom: 30px;
		figure {
			.news-img {
				display: block;
				@include bor-rad(5px);
				overflow: hidden;
				img {
					width: 100%; } }
			figcaption {
				padding: 10px 0;
				.news-name {
					@include fs(14rem);
					font-weight: 500;
					margin-bottom: 10px;
					a {
						color: $main;
						display: block;
						display: -webkit-box;
						height: 0.875rem*1.4*2;
						@include fs(14rem);
						line-height: 1.4;
						-webkit-line-clamp: 2;
						-webkit-box-orient: vertical;
						overflow: hidden;
						text-overflow: ellipsis;
						&:hover {
							color: $blue; } } }
				.news-des {
					@include fs(13rem);
					color: darken($white,60%);
					padding-left: 15px;
					border-left: 1px solid $green;
					display: block;
					display: -webkit-box;
					height: 0.8125rem*1.5*3;
					@include fs(13rem);
					line-height: 1.5;
					-webkit-line-clamp: 3;
					-webkit-box-orient: vertical;
					overflow: hidden;
					text-overflow: ellipsis; } } } }
	&.news-list2 {
		margin-bottom: 0px;
		.news-col {
			figure {
				figcaption {
					time {
						@include fs(13rem);
						color: darken($white,60%); }
					.news-name {
						a {
							height: auto;
							@include fs(16rem);
							font-weight: 600;
							max-height: 1rem*1.4*2; } } } } } }
	&.news-list3 {
		background: darken($white,3%);
		padding: 15px;
		@include bor-rad(5px);
		.news-col {
			margin-bottom: 10px;
			padding-bottom: 10px;
			border-bottom: 1px solid darken($white,10%);
			&:last-child {
				margin-bottom: 0; }
			figure {
				figcaption {
					padding: 0;
					.news-name {
						margin-bottom: 0;
						a {
							@include fs(13rem);
							font-weight: 500;
							max-height: 0.875rem*1.4*2; } }
					.news-des {
						padding: 0;
						border: none;
						display: none; } } } } }
	.btn-viewmore {
		display: block;
		margin-top: 10px;
		text-transform: uppercase;
		@include fs(12rem);
		font-weight: 600;
		color: $blue;
		&:after {
			content: '\f105';
			margin-left: 5px; }
		&:hover {
			color: $green; } } }

.pages {
	.pagination {
		@include flexbox;
		justify-content: center;
		li {
			+ li {
				margin-left: 10px; }
			a, span {
				display: block;
				width: 34px;
				height: 34px;
				line-height: 34px;
				text-align: center;
				@include bor-rad(5px);
				color: $main;
				font-weight: normal;
				background: darken($white,5%); }
			&.active {
				a, span {
					background: $green;
					color: $white; } } } } }

.news-page {
	@extend .about-section;
	.page-title {
		margin-bottom: 30px; }
	.content {
		+ .share {
			margin-bottom: 40px; } } }

.hot-news {
	margin-bottom: 20px;
	.news-col {
		margin-bottom: 20px;
		figure {
			.news-img {
				display: block;
				@include bor-rad(5px);
				overflow: hidden;
				img {
					width: 100%;
					height: 160px;
					@include bor-rad(5px);
					object-fit: cover; } }
			figcaption {
				padding: 10px 0;
				time {
					@include fs(13rem);
					color: darken($white,60%); }
				.news-name {
					@include fs(14rem);
					font-weight: 600;
					line-height: 1.5;
					display: -webkit-box;
					height: 0.875rem*1.5*2;
					-webkit-line-clamp: 2;
					-webkit-box-orient: vertical;
					overflow: hidden;
					text-overflow: ellipsis;
					a {
						color: $main;
						&:hover {
							color: $blue; } } } } }
		&.big {
			figure {
				.news-img {
					img {
						height: 425px; } }
				figcaption {
					.news-name {
						@include fs(16rem);
						text-transform: uppercase;
						font-weight: 600;
						height: 1rem*1.5*2; } } } } } }
.news-video {
	margin-bottom: 30px;
	.video {
		margin-bottom: 10px;
		padding-bottom: 10px;
		border-bottom: 1px solid darken($white,10%);
		.video-img {
			display: block;
			@include pos(rel,null);
			margin-bottom: 10px;
			@include bor-rad(5px);
			overflow: hidden;
			&:before {
				content: '\f04b';
				@include pos(abs, 0 null 0 0);
				right: 0;
				background: color-rgba($black,0.3);
				@include fs(32rem);
				color: $white;
				@include flexbox;
				justify-content: center;
				align-items: center; } }
		figcaption {}
		.video-name {
			@include fs(13rem);
			line-height: 1.4;
			text-transform: capitalize;
			font-weight: 500;
			padding-left: 20px;
			@include pos(rel,null);
			&:before {
				content: '\f1c8';
				margin-right: 5px;
				font-weight: normal;
				@include pos(abs, 2px null null 0); }
			a {
				color: $main;
				&:hover {
					color: $blue; } } } }
	.btn-viewmore {
		display: block;
		margin-top: 10px;
		text-transform: uppercase;
		@include fs(12rem);
		font-weight: 600;
		color: $blue;
		&:after {
			content: '\f105';
			margin-left: 5px; }
		&:hover {
			color: $green; } } }

.news-album {
	.album-slide {
		margin-bottom: 10px; }
	.album-nav {
		.slick-list {
			margin: 0px -5px; }
		.item {
			padding: 0px 5px; } }
	.album-img {
		display: block;
		@include bor-rad(5px);
		overflow: hidden; }
	.album-name {
		padding: 5px 0;
		@include fs(14rem);
		font-weight: 500;
		line-height: 1.4;
		a {
			color: $main;
			&:hover {
				color: $blue; } } }
	.btn-viewmore {
		display: block;
		margin-top: 10px;
		text-transform: uppercase;
		@include fs(12rem);
		font-weight: 600;
		color: $blue;
		&:after {
			content: '\f105';
			margin-left: 5px; }
		&:hover {
			color: $green; } } }
