.sidebar-category {
	border: 1px solid darken($white,10%);
	margin-bottom: 30px;
	ul {
		> li {
			border-bottom: 1px solid darken($white,10%);
			&:last-child {
				border-bottom: none; }
			> a {
				display: block;
				color: $main;
				padding: 9px 25px;
				@include fs(15rem);
				@include pos(rel,null); }
			&:hover {
				> a {
					color: $green; } }
			&.active {
				> a {
					color: $green;
					font-weight: 500;
					border-left: 4px solid $green;
					padding-left: 21px;
					margin-left: -1px; } } } } }

.top-nav {
	background: $white;
	ul.top-link {
		@include flexbox;
		justify-content: center;
		padding: 10px 0;
		@include media-breakpoint-down(md) {
			display: block;
			white-space: nowrap;
			padding: 5px 15px;
			overflow: auto; }
		li {
			@include media-breakpoint-down(md) {
				display: inline-block; }
			a {
				display: block;
				line-height: 28px;
				@include fs(14rem);
				text-transform: uppercase;
				text-align: center;
				font-weight: 600;
				color: $main;
				border: 1px solid transparent;
				padding: 0px 20px;
				@include bor-rad(50px);
				@include media-breakpoint-down(md) {
					@include fs(12rem); }
				&.mPS2id-highlight {
					border-color: $green;
					color: $green; } }
			&.active {
				a {
					border-color: $green;
					color: $green; } } } } }
.child-nav {
	background: $white;
	border-bottom: 1px solid darken($white,10%);
	margin-bottom: -1px;
	@include pos(rel,null);
	z-index: 50;
	ul {
		border-top: 1px solid darken($white,10%);
		@include flexbox;
		justify-content: center;
		@include media-breakpoint-down(lg) {
			flex-wrap: wrap;
			border-bottom: none;
			justify-content: flex-start; }
		li {
			// flex: auto
			flex: auto;
			@include media-breakpoint-down(lg) {
				width: 33.33%;
				flex: inherit; }
			@include media-breakpoint-down(sm) {
				width: 50%; }
			@include media-breakpoint-down(xs) {
				width: 100%; }
			a {
				display: block;
				text-align: center;
				@include fs(14rem);
				font-weight: 500;
				color: $blue;
				padding: 15px 0px;
				@include pos(rel,null);
				@include media-breakpoint-down(lg);
				@include media-breakpoint-down(sm);
				@include media-breakpoint-down(xs);
				&:after {
					content: '';
					display: block;
					width: 0px;
					height: 2px;
					background: $green;
					@include pos(abs, null null -1px 50%);
					@include transform(translate(-50%,0));
					@include transition(all,0.5s);
					@include media-breakpoint-down(lg) {
						display: none; } }
				&:hover {
					&:after {
						width: 60px; } }
				&.mPS2id-highlight {
					color: $green;
					&:after {
						width: 60px; } } } } } }
.about-title {
	@include fs(24rem);
	font-weight: 700;
	color: $blue;
	text-transform: uppercase;
	margin-bottom: 30px;
	&:after {
		content: '';
		display: block;
		width: 60px;
		height: 1px;
		background: $blue;
		margin-top: 10px; }
	&.text-center {
		&:after {
			margin: 10px auto 0; } } }
.about-stitle {
	@include fs(24rem);
	line-height: 1.3;
	font-weight: 500;
	color: $main;
	margin-bottom: 20px; }
.about-section {
	border-top: 1px solid darken($white,10%);
	background: url(../img/corp/aboutbg.png) top left repeat-x;
	padding: 50px 0;
	overflow: hidden;
	@include pos(rel,null); }
.about-intro {
	.vision-img {
		text-align: center;
		margin-bottom: 70px;
		@include media-breakpoint-down(md) {
			margin-bottom: 40px; }
		@include media-breakpoint-down(sm) {
			margin-bottom: 20px; } }
	.vision-content {
		padding: 30px 0;
		.vision-title {
			@include fs(18rem);
			line-height: 1.5;
			text-transform: uppercase;
			text-align: justify;
			font-weight: 600;
			color: $main;
			margin-bottom: 20px;
			@include media-breakpoint-down(sm) {
				@include fs(16rem);
				margin-bottom: 15px; } }
		img {
			width: 100%; } }
	.content {
		@include fs(15rem);
		@include media-breakpoint-down(md) {
			@include fs(14rem); } } }
.about-value {
	.core-value {
		padding-bottom: 100px;
		@include media-breakpoint-down(md) {
			padding-bottom: 250px; }
		@include media-breakpoint-down(xs) {
			padding-bottom: 150px; }
		&:after {
			content: '';
			display: block;
			width: 1140px;
			height: 277px;
			background: url(../img/feed/introimg2.png) center center no-repeat;
			@include pos(abs, null null -80px 0);
			opacity: 0.2; }
		.value-content {
			.value-title {
				@include fs(24rem);
				line-height: 1.3;
				font-weight: 500;
				margin-bottom: 20px;
				@include media-breakpoint-down(md) {
					@include fs(20rem); }
				@include media-breakpoint-down(sm) {
					@include fs(18rem); } } }
		.value-img {
			@include pos(abs, null -250px 0 null);
			z-index: 1;
			@include media-breakpoint-down(lg) {
				width: 80%;
				right: -150px; }
			@include media-breakpoint-down(md);
			@include media-breakpoint-down(xs) {
				right: -100px; } }
		.intro-value {
			margin-bottom: 40px;
			@include media-breakpoint-down(sm) {
				margin-bottom: 0; }
			.value {
				margin-bottom: 30px;
				figure {
					.icon {
						@include flexbox;
						height: 80px;
						justify-content: center;
						align-items: center; }
					figcaption {
						padding: 20px 0 0;
						text-align: center;
						&:after {
							content: '';
							display: block;
							width: 50px;
							height: 1px;
							background: $green;
							margin: 0 auto; }
						.number {
							color: $blue;
							@include fs(36rem);
							margin-bottom: 5px;
							strong {
								display: inline-block;
								font-weight: 700;
								line-height: 1; }
							span {
								display: inline-block;
								margin-left: 5px;
								@include fs(22rem);
								font-weight: 500; } }
						.name {
							@include fs(16rem);
							text-transform: uppercase;
							line-height: 1.5;
							margin-bottom: 20px;
							color: darken($white,60%);
							@include media-breakpoint-down(lg) {
								@include fs(12rem); } } } } } } } }
.about-ceo {
	padding-bottom: 0;
	.ceo-message {
		padding-top: 30px;
		@include media-breakpoint-down(lg) {
			padding-top: 0; }
		.ceo-img {}
		.message-wrap {
			height: 100%;
			background: url(../img/corp/logobg.png) top center no-repeat; }
		.message-list {
			// background: url(../img/corp/logobg.png) top center no-repeat
			padding-top: 40px;
			@include media-breakpoint-down(lg) {
				padding-top: 30px; }
			@include media-breakpoint-down(md) {
				margin-bottom: 70px; }
			.slick-list {
				margin-bottom: 40px; }
			.item {
				.mess-title {
					@extend .about-stitle;
					line-height: 1.4;
					font-weight: 500; }
				.message {
					.content {
						// +fs(16rem)
						font-weight: 300;
						@include media-breakpoint-down(lg) {
							@include fs(15rem); } } }
				.ceo-info {
					text-align: right;
					margin-top: 30px;
					.ceo-name {
						@include fs(15rem);
						font-style: italic;
						@include media-breakpoint-down(lg) {
							@include fs(14rem); } } } }
			.slick-prev, .slick-next {
				@include pos(abs, null 0 -10px null);
				@include transform(translate(0,0));
				@include fs(0);
				z-index: 5;
				width: 40px;
				height: 40px;
				text-align: center;
				line-height: 38px;
				@include bor-rad(30px);
				background: $white;
				border: 1px solid darken($white,5%);
				// box-shadow: 0px 3px 10px color-rgba($black,0.05)
				&:before {
					content: '';
					@include fs(20rem); }
				&:hover {
					color: $green;
					background: lighten($green,60%);
					box-shadow: 0px 3px 10px color-rgba($black,0.05); } }
			.slick-prev {
				right: 45px;
				&:before {
					content: '\f104'; } }
			.slick-next {
				&:before {
					content: '\f105'; } }
			.slick-dots {
				li {
					display: inline-block;
					width: 10px;
					height: 10px;
					@include bor-rad(50px);
					border: 1px solid darken($white,15%);
					cursor: pointer;
					+ li {
						margin-left: 8px; }
					button {
						display: none; }
					&.slick-active {
						background: $green;
						border-color: $green; } } } } } }
.about-history {}
.about-company {
	.company-structure {
		.content {
			// +fs(16rem)
			a {
				color: $blue;
				&:hover {
					color: $green; } } } }
	.company-system {
		text-align: center;
		padding-bottom: 70px; } }
.about-award {
	.award-list {
		.award {
			margin-bottom: 10px;
			figure {
				.award-img {
					@include flexbox;
					height: 250px;
					justify-content: center;
					align-items: center;
					align-content: center;
					// background: darken($white,4%)
					img {
						max-width: 90%;
						max-height: 90%;
						@include transition(all,0.5s); } }
				figcaption {
					padding: 15px 0px;
					text-align: center;
					.award-name {
						@include fs(14rem);
						font-weight: 500;
						line-height: 1.5; } }
				&:hover {
					.award-img {
						img {
							@include transform(scale3d(1.1,1.1,1)); } } } } } }
	.about-history {
		padding-bottom: 70px; } }

.about-service {
	// background: url(../img/feed/aboutbg.jpg) top left repeat
	// +pos(rel,null)
	// &:after
	// 	content: ''
	// 	+pos(abs,0 null 0 0)
	// 	right: 0
	// 	background: linear-gradient(color-rgba($white,0), $white 35%)
	.page-title, .page-des {
		@include pos(rel,null);
		z-index: 5; }
	.service-list {
		@include pos(rel,null);
		z-index: 5;
		.row {
			margin: 0px -10px; }
		.col-12 {
			padding: 0px 10px; }
		.service-col {
			figure {
				@include pos(rel,null);
				.service-img {
					display: block;
					background: #00224d;
					@include pos(rel,null);
					&:before {
						content: '';
						@include pos(abs, 60% null 0 0);
						right: 0;
						background: linear-gradient(color-rgba($black,0),70%$black);
						opacity: 0.75; }
					img {
						width: 100%;
						opacity: 0.75;
						@include media-breakpoint-down(lg) {
							height: 300px;
							object-fit: cover; }
						@include media-breakpoint-down(sm) {
							height: auto; } } }
				figcaption {
					text-align: center;
					@include pos(abs, null 0 0 0);
					padding: 0px 30px 40px;
					@include media-breakpoint-down(md) {
						padding: 15px; }
					.service-name {
						@include fs(24rem);
						font-weight: 500;
						margin-bottom: 15px;
						@include media-breakpoint-down(lg) {
							@include fs(18rem);
							margin-bottom: 10px; }
						@include media-breakpoint-down(md) {
							@include fs(16rem); }
						a {
							color: $white; } }
					.service-des {
						@include fs(14rem);
						color: $white;
						@include media-breakpoint-down(lg) {
							@include fs(13rem); } } } } } } }

.about-improving {
	padding-bottom: 0;
	.improve-list {
		article {
			&:nth-child(odd) {
				@include media-breakpoint-up(lg) {
					.improve-col {
						figure {
							.improve-img {
								order: 2; }
							figcaption {
								order: 1;
								&:before {
									top: inherit;
									bottom: 0;
									@include transform(translate(-50%,50%)rotateZ(45deg)); } } } } } } }
		.improve-col {
			figure {
				@include flexbox;
				flex-wrap: wrap;
				.improve-img {
					width: 100%;
					.slick-dots {
						@include pos(abs, null 0 10px 0);
						text-align: center;
						li {
							display: inline-block;
							width: 10px;
							height: 10px;
							@include bor-rad(50px);
							background: $green;
							&.slick-active {
								background: $white; }
							+ li {
								margin-left: 10px; }
							button {
								display: none; } } }
					img {
						width: 100%; } }
				figcaption {
					width: 100%;
					height: 320px;
					@include flexbox;
					flex-wrap: wrap;
					justify-content: center;
					align-items: center;
					align-content: center;
					background: darken($white,4%);
					padding: 0px 70px;
					@include pos(rel,null);
					@include media-breakpoint-down(lg) {
						padding: 0px 20px;
						height: 270px; }
					@include media-breakpoint-down(md) {
						height: inherit;
						padding: 30px 40px; }
					@include media-breakpoint-down(sm) {
						padding: 20px; }
					&:before {
						content: '';
						display: block;
						width: 20px;
						height: 20px;
						background: darken($white,4%);
						@include pos(abs, 0 null null 70px);
						@include transform(translate(-50%,-50%)rotateZ(45deg)); }
					.improve-name {
						width: 100%;
						@include fs(24rem);
						font-weight: 600;
						color: $main;
						margin-bottom: 10px;
						@include media-breakpoint-down(lg) {
							@include fs(18rem); }
						@include media-breakpoint-down(sm) {
							@include fs(16rem); } }
					.improve-des {
						width: 100%;
						@include fs(14rem);
						// font-weight: 300
						line-height: 1.6;
						@include media-breakpoint-down(lg) {
							@include fs(13rem); } } } } } } }
.about-quality {
	padding: 70px 0;
	.quality-banner {
		margin-bottom: 40px; }
	.cert-list {
		.slick-prev, .slick-next {
			@include pos(abs, calc(50% - 40px) null null null);
			@include transform(translateY(-50%));
			@include fs(0);
			z-index: 5;
			opacity: 0.3;
			&:before {
				content: '';
				@include fs(36rem); }
			&:hover {
				opacity: 1; } }
		.slick-prev {
			left: 0;
			&:before {
				content: '\f104'; } }
		.slick-next {
			right: 0;
			&:before {
				content: '\f105'; } }
		.item {
			.cert {
				.cert-img {
					@include flexbox;
					justify-content: center;
					align-items: center;
					height: 240px;
					@include media-breakpoint-down(lg) {
						height: 170px; }
					img {
						max-width: 100%;
						max-height: 100%; } }
				figcaption {
					padding: 20px 0; }
				.cert-name {
					text-align: center;
					padding: 0px 20px;
					@include fs(14rem);
					line-height: 1.4;
					font-weight: 600;
					color: $main;
					@include media-breakpoint-down(lg) {
						@include fs(13rem);
						font-weight: 500; } } } } } }
